import React, { useEffect, useState } from "react";
import axios from "axios";
import "./App.css";
import Opt from "./options/option";
import Loader from "./loader/loader";
import Card from "./card/card";
import Top from "./topbar/top";
// https://api.edyoda.com/v1/blog/
// https://api.edyoda.com/v1/blog/postcategories/
// https://api.edyoda.com/v1/blog/artificial-intelligence/

const Home = () => {
  const [loader, setLoader] = useState(true);
  const [opt, setOpt] = useState([]);
  const [car, setCard] = useState([]);

  const adder = (r) => {
    console.log("entered");
    let z = document.getElementsByClassName("optm");
    z[0].classList.add("optC1");
    let y = document.getElementsByClassName("opt");
    for (let x = 0; x < y.length; x++) y[x].classList.remove("optC1");
  };

  useEffect(() => {
    axios.get("https://api.edyoda.com/v1/blog/postcategories/").then((res) => {
      setLoader(false);
      setOpt(res.data);
    });
    axios.get("https://api.edyoda.com/v1/blog/").then((res) => {
      setLoader(false);
      setCard(res.data);
    });
  }, []);

  const handle = (a) => {
    setLoader(true);
    if (a == "all") {
      const u = "https://api.edyoda.com/v1/blog/";
      axios.get(u).then((re) => {
        setLoader(false);
        setCard(re.data);
      });
    } else {
      const u = "https://api.edyoda.com/v1/blog/" + a + "/";
      axios.get(u).then((re) => {
        setLoader(false);
        setCard(re.data.posts);
      });
    }
  };

  return (
    <div className="home">
      {loader && <Loader />}

      <Top></Top>

      <div className="optC">
      <div>  <i className="filterIcon" class="fas fa-filter"></i> <span className="filterText"> Filter by Category</span></div>
        <div
          className="optm"
          onClick={() => {
            handle("all");
            adder();
          }}
        >
          All
        </div>
        {opt.map((a) => (
          <Opt ser={(e) => handle(e)} {...a} />
        ))}
      </div>
      <div className="cardC">
        {loader ? <Loader /> : car.map((a) => <Card {...a} />)}
      </div>
    </div>
  );
};

export default Home;
