import React, { useState } from "react";
import "../options/option.css";

const Opt = ({ id, slug, title, ser }) => {
    const [ss, sss] = useState(true);
    const adder = (r) => {
        let z = document.getElementsByClassName("optm");
        z[0].classList.remove("optC1");
        let y = document.getElementsByClassName("opt");
        for (let x = 0; x < y.length; x++)
            y[x].innerHTML == r
                ? y[x].classList.add("optC1")
                : y[x].classList.remove("optC1");
    };

    return (
        <div
            key={id}
            className="opt"
            onClick={(e) => {
                sss(false);
                ser(slug);
                adder(e.target.innerText);
            }}
        >
            {title}
        </div>
    );
};

export default Opt;
